import { Injectable } from '@angular/core';
import { OrganisationService } from '@placeos/organisation';
import {
    PlaceMetadata,
    showMetadata,
    updateMetadata,
} from '@placeos/ts-client';
import { BehaviorSubject, combineLatest, of } from 'rxjs';
import {
    catchError,
    filter,
    map,
    shareReplay,
    switchMap,
} from 'rxjs/operators';

export interface LockerBank {
    id: string;
    map_id: string;
    level_id: string;
    name: string;
    height: number;
    lockers: Locker[];
}

export interface Locker {
    id: string;
    bank_id: string;
    name: string;
    accessible: boolean;
    bookable: boolean;
    position: [number, number];
    size: [number, number];
}

@Injectable({
    providedIn: 'root',
})
export class LockersService {
    private _level = new BehaviorSubject('');
    private _change = new BehaviorSubject(0);
    public readonly lockers_banks$ = combineLatest([
        this._org.active_building,
        this._change,
    ]).pipe(
        filter(([bld]) => !!bld),
        switchMap(([bld]) =>
            showMetadata(bld.id, 'lockers').pipe(
                catchError(() => of(new PlaceMetadata()))
            )
        ),
        map(
            (_) =>
                ((_.details instanceof Array ? _.details : null) ||
                    []) as LockerBank[]
        ),
        shareReplay(1)
    );

    public readonly lockers$ = this.lockers_banks$.pipe(
        map((bank_list) => {
            console.log('Bank List', bank_list);
            const lockers = [];
            for (const bank of bank_list) {
                lockers.push(
                    ...bank.lockers.map(
                        (_) => ({ ..._, bank_id: bank.id } as Locker)
                    )
                );
            }
            return lockers;
        })
    );

    public readonly filtered_lockers$ = combineLatest([
        this._level,
        this.lockers$,
    ]).pipe(
        map(([level, lockers]) =>
            lockers.filter(
                (_) => (!level || _.level_id === level) && _.bookable
            )
        )
    );

    public setLevel(level: string) {
        this._level.next(level);
    }

    constructor(private _org: OrganisationService) {}

    public async saveLockers(lockers: LockerBank[]) {
        await updateMetadata(this._org.building.id, {
            name: 'lockers',
            description: '',
            details: lockers,
        });
        this._change.next(Date.now());
    }
}
